import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import style from "../StudentAdmissionFees/StudentAdmissionFees.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  assestsFeedUrl,
  assestsNavbarUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import styleAdmission from "../AdmissionManager/AdmissionManager.module.css";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import { useInstituteAllStudent } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useState } from "react";
import styleFinance from "../FinanceManager/FinanceManager.module.css";
import { useStudentLeaving } from "../../../hooks/institute/institute-staff-student-api";
import QvipleLoader from "../../Loader/QvipleLoader";

const CertificateStudentFormSearch = ({ onCloseBackdrop, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [studentLeaving] = useStudentLeaving();

  const { instituteAllStudent, instituteAllStudentLoading } =
    useInstituteAllStudent({
      data: {
        id: instituteId,
        page: 1,
        limit: 10,
        search: search,
      },
      skip: search ? !instituteId : true,
    });

  const onSearchEvent = (e) => {
    setSearchCopy(e.target.value);
  };

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setSearch(searchCopy);
    }
  };

  const onViewApplicantProfile = (val) => {
    if (getQuery.state?.is_bonafide) {
      navigate(`/q/${params.username}/student/certificate/form`, {
        state: {
          ...getQuery.state,
          studentId: val?._id,
        },
      });
    } else {
      if (instituteId && val?.studentGRNO) {
        setDisabled((pre) => !pre);
        studentLeaving({
          id: instituteId,
          gr: val?.studentGRNO,
          leavingFeedback: {
            number: val?.studentGRNO,
            studentCertificateNo: "",
            leaving_date: "",
            bookNo: "",
            studentUidaiNumber: "",
            studentPreviousSchool: "",
            studentLeavingBehaviour: "",
            studentLeavingStudy: "",
            studentLeavingReason: "",
            studentRemark: "",
            instituteJoinDate: "",
            instituteLeavingDate: "",
            leaving_degree: "",
            leaving_since_date: "",
            leaving_course_duration: "",
            elective_subject_one: "",
            elective_subject_second: "",
            leaving_project_work: "",
            leaving_guide_name: "",
          },
        })
          .then((res) => {
            setDisabled((pre) => !pre);
            if (getQuery.state?.is_leaving === "leaving") {
              navigate(`/q/${params.username}/student/certificate/form`, {
                state: {
                  ...getQuery.state,
                  student: res.data.student,
                  download: res.data.download,
                },
              });
            } else if (getQuery.state?.is_leaving === "MTC") {
              navigate(`/q/${params.username}/student/certificate/form`, {
                state: {
                  ...getQuery.state,
                  student: res.data.student,
                  download: res.data.download,
                },
              });
            } else if (getQuery.state?.is_leaving === "MLC") {
              navigate(`/q/${params.username}/student/certificate/form`, {
                state: {
                  ...getQuery.state,
                  student: res.data.student,
                  download: res.data.download,
                },
              });
            } else {
              navigate(`/q/${params.username}/student/certificate/form`, {
                state: {
                  ...getQuery.state,
                  student: res.data.student,
                  download: res.data.download,
                },
              });
            }
          })
          .catch({});
      }
    }
    onCloseBackdrop();
  };

  return (
    <>
      <div className={style.saps_container}>
        <div
          className={styleFinance.fm_search_full}
          style={{
            position: "sticky",
            top: "0",
          }}
        >
          <section className={styleFinance.fm_search_full_container}>
            <div className={styleFinance.fm_search_full_container_inner}>
              <img
                className={styleFinance.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
                autoFocus
                style={{
                  borderTop: "0",
                  borderBottomLeftRadius: "0",
                  borderBottomRightRadius: "0",
                }}
              />
            </div>
          </section>
        </div>

        {instituteAllStudentLoading ? (
          <Loading />
        ) : (
          instituteAllStudent?.list?.map((student, index) => (
            <div
              className={styleAdmission.apf_container}
              onClick={() => onViewApplicantProfile(student)}
              key={`${student?._id}_${index}`}
            >
              <div className={styleAdmission.apf_container_inner}>
                <img
                  alt="Student Avatar"
                  src={
                    student?.studentProfilePhoto
                      ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  loading="lazy"
                />
                <div className={styleAdmission.apfci_text}>
                  <h6>{`${student.studentFirstName ?? ""} ${
                    student.studentMiddleName ? student.studentMiddleName : ""
                  } ${student.studentLastName ?? ""}`}</h6>

                  <p>
                    {t("gr_no")}
                    {student?.studentGRNO ?? ""}
                    {student?.department?.dName
                      ? `, ${student?.department?.dName}`
                      : ""}
                  </p>
                </div>
              </div>
            </div>
          ))
        )}

        {!instituteAllStudentLoading &&
          instituteAllStudent?.list?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No student found related this search."
            />
          )}
      </div>

      {disabled && <QvipleLoader />}
    </>
  );
};

export default CertificateStudentFormSearch;

const onTotalStudentFees = (arg, arg1) => {
  return arg + arg1;
};
